.sectionHeaderWrapper {
    display: flex;
    flex-direction: column;
    width: 640px;
    .sectionHeader {
        display: flex;
        flex-direction: row;
        font-family: 'Roboto Slab';
        white-space: pre;
        p {
            font-size: 16px;
            color: #51edf5;
            margin-right: 15px;
            line-height: 22px;
        }
        span {
            font-size: 24px;
            font-weight: 700;
            color: #dae3f2;
            margin-right: 15px;
            line-height: 20px;
        }
        hr {
            width: 100%;
            height: 5px;
            border: none;
            border-bottom: 1px dotted #293b61;
        }
    }
    .sectionContent {
        margin-top: 10px;
    }
}