.sideMenu {
    background-color: #232f45;
    width: 65px;
    height: 100%;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 2;
    nav {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        button {
            width: 65px;
            height: 65px;
            border: none;
            margin-bottom: 1px;
            background-color: rgba(255, 255, 255, 0.03);
            outline: none;
            transition: 0.2s all;
            path {
                fill: rgba(218, 227, 242, 0.5);
            }
            &:hover {
                background-color: rgba(255, 255, 255, 0.1);
            }
            &.activeSection {
                background-color: #51edf5;
                path {
                    fill: #232f45;
                }
            }
        }
    }
}